import { useContext, useEffect } from "react";
import MapContext from "../Map/MapContext";
import ImageLayer from "ol/layer/Image";
const GeoserverLayer = ({ source, title, opacity=0.5, zIndex = 0 }) => {
  const { map } = useContext(MapContext); 
  useEffect(() => {
    if (!map) return;
    
    let geoserverlayer = new ImageLayer({
      source,
      title,
      opacity,
      zIndex,
    });
    map.addLayer(geoserverlayer);
    geoserverlayer.setZIndex(zIndex);
    return () => {
      if (map) {
        map.removeLayer(geoserverlayer);
      }
    };
  }, [map]);
  return null;
};
export default GeoserverLayer;